$box-size: 100px;
$box-gap: 1px;
$width-of-calendar: $box-size * 7 + $box-gap * 6;

$margins-for-day: $box-gap + $box-size;

$week: (
  mon: 0,
  tue: $margins-for-day,
  wed: $margins-for-day * 2,
  thu: $margins-for-day * 3,
  fri: $margins-for-day * 4,
  sat: $margins-for-day * 5,
  sun: $margins-for-day * 6,
);
